// ===============================================
// TEXT COMPONENT
// ===============================================


/*
	 font-size, font-weight  line-height  , 
	  $name.
	   $screen-sizes   ,
	     .    
	  $grid-breakpoints (/scss/variables/_breakpoints.scss).

	create-text-properties(text-lg, sm) :

		--text-lg-fz: var(--df_text-lg-fz);
		--text-lg-lh: var(--df_text-lg-lh);

		font-size: var(--text-lg-fz);
		line-height: var(--text-lg-lh);

		@media (min-width:576px) {
			--text-lg-fz: var(--sm_text-lg-fz);
			--text-lg-lh: var(--sm_text-lg-lh);
		}


	text-component(h2) :

		font-size: var(--h2-fz);
		line-height: var(--h2-lh);
*/

/*
	TODO:
	-      ;
	-        ;
	-     xl:text-md;
*/

@mixin create-text-properties($name, $screen-sizes: []) {
	@if (length($screen-sizes) > 0) {
		--#{$name}-fz: var(--df_#{$name}-fz);
		// --#{$name}-lh: var(--df_#{$name}-lh);
	}

	// stylelint-disable-next-line order/order
	font-size: var(--#{$name}-fz);
	line-height: var(--#{$name}-lh);

	@if (check-if-heading($name) == true) {
		font-weight: var(--#{$name}-fw);
	}

	@if (length($screen-sizes) > 0) {
		@each $screen-size in $screen-sizes {
			@include media-breakpoint-up($screen-size) {
				--#{$name}-fz: var(--#{$screen-size}_#{$name}-fz);
				// --#{$name}-lh: var(--#{$screen-size}_#{$name}-lh);
			}
		}
	}
}
